import React from 'react'
import { navigateTo } from 'gatsby-link'
import 'semantic-ui-css/semantic.min.css'
import ps from 'zxcvbn'
import { Grid, Header, Segment, Item, Button, List, Container } from 'semantic-ui-react'
import Login from '../../components/login.js'
import Split from '../../components/split.js'
import geturlparameter from '../../utils/geturlparamater'

let scoreMeaning = [
  'too easily guessed',
  'very easily guessed',
  'somewhat easily guessed',
  'safe',
  'very safe'
]

class Diagnostic extends React.Component {
  constructor (props) {
    super(props)
    this.state = {}
    this.state.pass = geturlparameter('pass', this.props.location.search) || ''
  }
  componentDidMount () {
    this.passwordInput.focus()
  }
  render () {
    const { pass } = this.state
    const info = ps(this.state.pass)
    return (
      <Split left={{ verticalAlign: 'middle', width: 5 }}>
        <Container>
          <Segment attached>
            <Header color='teal'>Try experimenting</Header>
            <Login onSubmit={e => { navigateTo(`/diagnostic/diagnostic?pass=${this.state.pass}`) }}>
              <Login.Password
                reference={el => { this.passwordInput = el }}
                onChange={e => { this.setState({ pass: e.target.value }) }}
                value={pass}
                type='text' />
              <Grid columns={2}>
                <Grid.Column width={4}>
                  <Button onClick={e => navigateTo(`/generate/start/`)}>
                    Next
                  </Button>
                </Grid.Column>
              </Grid>
            </Login>
          </Segment>
          <Segment attached>
            {
              this.state.pass.length
                ? <Grid.Column floated='left' textAlign='left' width='12'>
                  <List bulleted>
                    <Item>
                        Score: {info.score} / 4 {scoreMeaning[info.score]}
                    </Item>
                    {
                      info.score >= 3
                        ? (<Item> This password is pretty good! It's very hard to get hacked.</Item>)
                        : (<Item> This password is not secure enough. Try to get a score of 3 out of 4.</Item>)
                    }
                    {
                      info.feedback.suggestions.map((s, i) =>
                        <Item key={i}>Tips: {s}</Item>
                      )
                    }
                  </List>
                </Grid.Column>
                : null
            }
          </Segment>
        </Container>
        <Segment>
          <Header>To crack your password, it would take</Header>
          <Item.Group items={[
            {
              description: 'On a secure website that limits the number of password attempts:',
              header: `${info.crack_times_display.online_throttling_100_per_hour}`
              // meta: 'at 100 guesses an hour'
            },
            {
              description: 'On a secure website:',
              header: `${info.crack_times_display.online_no_throttling_10_per_second}`
              // meta: 'at 10 guesses a second'
            },

            {
              description: 'On a website that was compromised:',
              header: `${info.crack_times_display.offline_fast_hashing_1e10_per_second}`
              // meta: "at 10000000000 guesses a second"
            }
          ]} />
        </Segment>
      </Split>
    )
  }
}
export default Diagnostic



